@import "mixins";

#navbar {
    border: unset;
    background-color: #7850b8;
    background-image: linear-gradient(to bottom, #a879f5 0%, #7850b8 100%);

    a.navbar-brand {
        color: white;
        line-height: 1;
        font-variant: small-caps;
        font-style: italic;
        font-weight: bold;
        // fluid-type values: ($min_width, $max_width, $min_font, $max_font)
        @include fluid-type(740px, 1200px, 24px, 38px);
    }

    p.navbar-text {
        color: white;
        margin-top: 7px;
        word-spacing: 0.1em;
        font-family: 'Georgia', 'Times New Roman', 'Times', 'serif';
        font-style: italic;
        // fluid-type values: ($min_width, $max_width, $min_font, $max_font)
        @include fluid-type(740px, 1200px, 14px, 24px);
    }
}